import React, {useState, useEffect} from 'react'
import Pancard from '../../assets/images/Pan card.svg'
import Address from '../../assets/images/Address.svg'
import Bank from '../../assets/images/Bank.svg'
import Demat from '../../assets/images/Demat.svg'
const HowToBuy = () => {
  const [switchValue, setSwitchvalue]= useState(false)
  const [screenWidth,setScreenwidth] = useState('desktop')

  // useEffect(()=>{
  //   let scr = window.screen.width
  //   if(window.screen.width > 480 || window.screen.width < 320){
  //     setScreenwidth('desktop')
  //   }else{
  //     setScreenwidth('mobile')
  //   }
  // },[screenWidth])
 
    const handleSwitch = () =>{
      setSwitchvalue(!switchValue)
      console.log(switchValue)
      

      // for desktop switch
      if (screenWidth === 'desktop') {
        if(switchValue){
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.left = "0rem";
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.color = "#f6821f";
          
          setTimeout(()=>{
            document.querySelector(".how-to-buy .switch-container .switch ").innerText =
            "Buy";
            document.querySelector(
              ".how-to-buy .switch-container .switch "
            ).style.color = "#fff";
          },500)
          
        }else{
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.left = "8rem";
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.color = "#f6821f";
          setTimeout(()=>{
            document.querySelector(".how-to-buy .switch-container .switch ").innerText =
            "Sell";
            document.querySelector(
              ".how-to-buy .switch-container .switch "
            ).style.color = "#fff";
          },500)
        }
        
        // filter = 1;
      } else if (screenWidth === 'mobile') {
        if(switchValue){
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.left = "0rem";
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.color = "#f6821f";
          
          setTimeout(()=>{
            document.querySelector(".how-to-buy .switch-container .switch ").innerText =
            "Buy";
            document.querySelector(
              ".how-to-buy .switch-container .switch "
            ).style.color = "#fff";
          },500)
          
        }else{
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.left = "18rem";
          document.querySelector(
            ".how-to-buy .switch-container .switch "
          ).style.color = "#f6821f";
          setTimeout(()=>{
            document.querySelector(".how-to-buy .switch-container .switch ").innerText =
            "Sell";
            document.querySelector(
              ".how-to-buy .switch-container .switch "
            ).style.color = "#fff";
          },500)
        }
        // filter = 1;
      }

      
      
    }
    return (
        <section id="how-to-buy">
      <div className="how-to-buy" id="how-to-buy-sell-card">
        <div className='how-to-buy-header' id='how-to-buy-header'>
        <div className='how-to-buy-header-subs'>
        <h1>How to buy or sell ETF?</h1>
        <p>
        ETFs can be bought or sold in two ways – on a stock exchange or directly<br className="desktop-line-breaks" />
        with an AMC. Buying / Selling units of ETF on the stock exchange is similar<br className="desktop-line-breaks" />
        to buying / selling of any other listed securities.
        </p>
        </div>
      
        <div className="switch-container" onClick={handleSwitch}>
          <span>Buy</span>
          <span>Sell</span>
          <div className="switch">Buy</div>
        </div> 
        </div>




        <div className="how-to-buy-info">
          <div className="process">
            <div className="how-to-buy-heading">
              <h1>The Process</h1>
            </div>
            <li className="process-1">
              <p>1</p>
              <div className="process-info">
                <h1>Open a Demat Account and a Trading Account</h1>
                {/* <p>
                  Select an ETF Offering and create an
                  <br className="mobile-line-breaks" />
                  account on iPru AMC
                </p> */}
                <p>
                You need to open a trading account with a broker<br className="mobile-line-breaks" />/sub-broker. <br className='desktop-line-breaks' />
                You will also be required to have demat account for holding <br className='desktop-line-breaks'/>
                the ETF units.
                </p>
              </div>
            </li>
            <li className="process-2">
              <p>2</p>
              <div className="process-info">
                <h1>Select an ETF</h1>
                {/* <p>
                  Choose between SIP & Lumpsum <br className="mobile-line-breaks" />
                  payment - Frequency of <br className="desktop-line-breaks" />
                  Payment
                </p> */}
                <p>
                Click on the specific scheme you had purchased your units <br className='desktop-line-breaks'/>
                in earlier and would now like to redeem.
                </p>
              </div>
            </li>
            <li className="process-3">
              <p>3</p>
              <div className="process-info">
                <h1>Online Transaction</h1>
                {/* <p>
                  Enter your personal details such as PAN,
                  <br className="mobile-line-breaks" />
                  Demat A/c No., etc. <br className="desktop-line-breaks" />
                  Keep your <br className="mobile-line-breaks" />
                  documents handy.
                </p> */}
                <p>
                Enter all the required details such as your PAN Number, <br className="desktop-line-breaks" />
                Quantity of units you’d like to sell (out of the purchased <br className="desktop-line-breaks" />
                units) Price etc. on the respective broker’s website and place <br className="desktop-line-breaks" />
                your sale order.
                </p>
              </div>
            </li>
            <li className="process-4">
              <p>4</p>
              <div className="process-info">
                <h1>Transaction through Telephonic Mode</h1>
                {/* <p>
                  Enter your personal details such as PAN,
                  <br className="mobile-line-breaks" />
                  Demat A/c No., etc. <br className="desktop-line-breaks" />
                  Keep your <br className="mobile-line-breaks" />
                  documents handy.
                </p> */}
                <p>
                Alternatively, you can also place your order to sell your units <br className="desktop-line-breaks" />
                by calling your broker and informing them about your trade <br className="desktop-line-breaks" />
                specifications. You will have to deliver the units to the broker <br className="desktop-line-breaks" />
                or sub-broker before the securities pay-in day of <br className="desktop-line-breaks" />
                the settlement cycle on the exchange.
                </p>
              </div>
            </li>
            <li className="process-5">
              <p>5</p>
              <div className="process-info">
                <h1>Units Credited to your Bank Account</h1>
                {/* <p>
                  Enter your personal details such as PAN,
                  <br className="mobile-line-breaks" />
                  Demat A/c No., etc. <br className="desktop-line-breaks" />
                  Keep your <br className="mobile-line-breaks" />
                  documents handy.
                </p> */}
                <p>
                Once you have sold your units, the proceeds of your sale will <br className="desktop-line-breaks" />
                be credited to your designated bank account.
                </p>
              </div>
            </li>
          </div>

          <div className="documents">
            <div className="how-to-buy-heading">
              <h1>Required Documents</h1>
              <p>
                You need to be KYC Compliant and will be required <br className='desktop-line-break'/>
                to furnish the following documents
              </p>
            </div>
            <div className="documents-container">
              <div className="document">
                <img src={Pancard} alt="" />
                <div className='document-desp'>
                <p>Proof of Identity</p>
                <span>PAN Card</span>
                </div>
              </div>
              <div className="document">
                <img src={Address} alt="" />
                <div className='document-desp'>
                <p>Proof of Address</p>
                <span>Passport</span>
                </div>
              </div>
              <div className="document">
                <img src={Bank} alt="" />
                <div className='document-desp'>
                <p>Bak Account Details</p>
                <span>Bank Account Statement</span>
                <span>Cancelled Cheque</span>
                </div>
              </div>
              
              
            </div>
          </div>
        </div>
      </div>
    </section>
    )
}

export default HowToBuy
