import React, {useRef} from 'react'

const WhyToInvest = () => {
  const sectionRef = useRef('why-to-invest')
    return (
      <section id="why-to-invest" ref={sectionRef}>
        <div className="why-to-invest">
          <div className="why-to-invest-info">
            <h1>Why you should invest <br/>in ETF?</h1>
            <div className="pointer-container">
              <div className="pointer">

                  <p className="pointer-count">1</p>


                  <p className="pointer-head">Cost Efficient</p>
                  <p className="pointer-subtext">
                  Being passively managed, its expense ratio is <br />
                  generally lower in comparison to traditional <br />
                  mutual funds.
                  </p>

              </div>
              <div className="pointer">

                  <p className="pointer-count">2</p>


                  <p className="pointer-head">Diversification</p>
                  <p className="pointer-subtext">
                  Investment in an ETF allows you to spread <br/>
                  your investments across various asset <br/>
                  classNamees and securities which can help <br/>
                  minimize the overall risk of your portfolio. <br/>
                  </p>

              </div>
              <div className="pointer">

                  <p className="pointer-count">3</p>


                  <p className="pointer-head">Liquidity</p>
                  <p className="pointer-subtext">
                  ETFs are open ended schemes so they do not have <br />
                  any lock in period hence providing you with higher <br />
                  liquidity as in case of emergency where you can sell <br />
                  your units at any given point of time during the <br />
                  market hours to meet the cash requirement.
                  </p>
 
              </div>
              <div className="pointer">

                  <p className="pointer-count">4</p>


                  <p className="pointer-head">Transparency</p>
                  <p className="pointer-subtext">
                  Since ETFs track an existing index, the portfolio <br />
                  details are publicly available on the exchanges.
                  </p>
 
              </div>
              <div className="pointer">

                  <p className="pointer-count">5</p>


                  <p className="pointer-head">Flexibility</p>
                  <p className="pointer-subtext">
                  Since they are listed and traded on stock <br />
                  exchange, you can buy and sell your units <br />
                  anytime you want at real time NAV during  <br />
                  the trading day.
                  </p>
 
              </div>
            </div>
          </div>
        </div>
      </section>
    )
}

export default WhyToInvest
