import React from 'react'
import Spinner from '../../assets/images/spinners/primary-spinner.svg'
import './primaryspinner.css'
const PrimarySpinner = () => {
  return (
    <div className='pspinner-container'>
        <img src={Spinner} alt='primary-spinner' id='pspinner' />
    </div>
  )
}

export default PrimarySpinner