import React from 'react'
import {Link} from 'react-router-dom'
import HomeNavbarSecondary from '../navbars/HomeNavbarSecondary'
import Rect240 from '../../assets/images/v1.PNG'
import PlayBtnIcon from '../../assets/images/play-button.svg'
import Ellipse75 from '../../assets/images/Ellipse 75.svg'
import Ellipse76 from '../../assets/images/Ellipse 76.svg'
import Avatar from '../../assets/images/Group 15007.svg'
import Italio from '../../assets/images/new_avatar_hero_showcase.svg'
const HomeHeroShowcase = () => {
    return (
      <div className="header-info">
        <div className="info">
          <h1>
           
              ETFs: Buy and Sell anytime <br className="desktop-line-breaks"/>for better returns
            
          </h1>
              <div className="info-links">
                <Link to='/explore'>Explore</Link>
                <a href="#how-to-choose">Get Help to choose</a>
              </div>
              <div className="video">
                  <div>
                    <a href='https://www.youtube.com/watch?v=RITUzloqncM' target='_blank' rel="noreferrer noopener">
                    <img src={Rect240} alt=""/>
                    <img src={PlayBtnIcon} alt=""/>
                    </a>
                  </div>
              <div>
              <h3>What are ETFs?</h3>
              <p>Watch to learn more <br className="mobile-line-breaks"/>about ETFs</p>
            </div>
              </div>
      </div>
      <div className="images">
        <img src={Ellipse75} alt=""/>
        <img src={Ellipse76} alt=""/>
        <img src={Avatar} alt=""/>
        <img src={Italio} alt=""/>
      </div>
      <HomeNavbarSecondary />
    </div>
    )
}

export default HomeHeroShowcase
