import React, {useState,useEffect} from 'react'
import axios from 'axios'
import {Link} from 'react-router-dom'
import ScrollAnimation from 'react-animate-on-scroll';
import FlowerIcon from '../../assets/images/Flower.svg'
import PiggyIcon from '../../assets/images/Piggy Bank.svg'

const StarterETF = () => {
    const [niftyData,setNiftydata] = useState({})
    const [sensexData,setSensexdata] = useState({})
    // axios.defaults.headers.post['Content-Type'] ='application/x-www-form-urlencoded';
    
    useEffect(()=>{
      fetchAllData(2038)
      fetchAllData('SPICE')
    },[])

    const fetchAllData = (fundCode) =>{
      handleData(fundCode)
    }

    const handleData = (fundCode) =>{
      let result = {}
      const urls =['/api1/LandingPage/','/api1/Performance_Default/','/api1/Fund_Details/']
      const fetchUrl = (url,fundCode) =>{
        return axios.post(`${url}${fundCode}`,
      {"request":"",
      },
      )
      }
      const promiseArr = urls.map(url => fetchUrl(url,fundCode))
      Promise.all(promiseArr)
      .then((data) =>{
        
        result.nav = data[0].data[0].NAV
        result.returns = data[1].data[1].Year_1
        result.fund_detail_extra = data[2]
        
        // console.log(result)
        
      })
      .finally(()=>{
        if(fundCode === 'SPICE'){
          setSensexdata(result)
        }else{
          setNiftydata(result)  
        }
      })
      .catch(err => console.log(err))
      
    
    }
    
    return (
      <section id="starter-etfs">
        <div className="starter-etfs">
          <div className="nifty-sensex-container">
            <div className="card-1">
              <div>
                <p className="new-investor">FOR NEW INVESTORS</p>
                <h1 className="investor-heading">Starter ETFs</h1>
              </div>
              <ScrollAnimation animateIn="fadeInRight" animateOnce={true} delay={0}>
              <div className="card-3">
                <Link to='/details/2038'>
                <div className="nifty-sensex-text">
                  <h1 className="light-heading">Nifty</h1>
                </div>
                <div className="card-2">
                  <div className="card">
                    <div className="card-info">
                      <p>1Y Returns</p>
                      <h1 className="main-heading">
                        {niftyData && (String(niftyData.returns).charAt(0) !== '-') && '+'}
                        {niftyData && Number(niftyData.returns).toFixed(2)}%
                      </h1>
                    </div>
                    <img className="vector-img" src={FlowerIcon} alt=""/>
                    <div className="card-bg">
                      <div className="card-img"></div>
                    </div>
                    <div className="card-details">
                      <p>ICICI Pru Nifty <br/>50 ETF</p>
                      <p><span>NAV</span>₹{niftyData && Number(niftyData.nav).toFixed(2)}</p>
                    </div>
                  </div>
                </div>
                </Link>
              </div>
              </ScrollAnimation>
            </div>

            {/* card two */}
            
            <div className="card-1">
              <div>
                <p className="new-investor">FOR NEW INVESTORS</p>
                <h1 className="investor-heading">Low Investment ETFs</h1>
              </div>
              <ScrollAnimation animateIn="fadeInRight" animateOnce={true} delay={2}>
              <div className="card-3">
                <Link to='/details/SPICE'>
                  <div className="nifty-sensex-text">
                    <h1 className="light-heading">Sensex</h1>
                  </div>
                  <div className="card-2">
                    <div className="card">
                      <div className="card-info">
                        <p>1Y Returns</p><h1 className="main-heading">+{sensexData && Number(sensexData.returns).toFixed(2)}%</h1>
                      </div>
                      <img className="vector-img piggy" src={PiggyIcon} alt=""/>
                      <div className="card-bg">
                        <div className="card-img"></div>
                      </div>
                      <div className="card-details">
                        <p>ICICI Pru<br/>Sensex ETF</p><p><span>NAV</span>₹{sensexData && Number(sensexData.nav).toFixed(2)}</p>
                      </div>
                    </div>
                  </div>
                </Link>
              
              </div>
              </ScrollAnimation>
            </div>
          
          </div>
        </div>
      </section>
    )
}

export default StarterETF
