function overview() {
  let listOptions = document.querySelectorAll(".main-navbar li");
let graphSection = document.querySelector("#overview-graph");
let managerSection = document.querySelector("#managers");
let indicatorSection = document.querySelector("#indicators");
  listOptions.forEach((option) => {
    option.classList.remove("active-option");
  });
  listOptions[0].classList.add("active-option");
  graphSection.style.display = "block";
  managerSection.style.display = "none";
  indicatorSection.style.display = "none";
}

function manager() {
  let listOptions = document.querySelectorAll(".main-navbar li");
let graphSection = document.querySelector("#overview-graph");
let managerSection = document.querySelector("#managers");
let indicatorSection = document.querySelector("#indicators");
  listOptions.forEach((option) => {
    option.classList.remove("active-option");
  });
  listOptions[1].classList.add("active-option");
  graphSection.style.display = "none";
  managerSection.style.display = "block";
  indicatorSection.style.display = "none";
}

