import React from 'react';
import '../css/ExplorePageSideContent.css'
import Stars from '../../../assets/images/star.svg'
// import Halfstar from '../../../assets/images/half-star.svg'
import Blankstar from '../../../assets/images/blank-star.svg'

const ExplorePageSideContent = () => {
  return <div class="side-content">
              <div class="filters-head">
                <h2>Filters</h2>
                <p>Clear All</p>
              </div>
              <hr class="filters-hr" />
              <div class="individual-etfs-filters">
                <div class="percentage-ret">
                  <h2>Percentage Return (CAGR)</h2>
                  <div class="return-input-bars">
                    <div class="indivi-return-input-slider-track"></div>
                    <input
                      type="range"
                      min="0"
                      max="25"
                      value="0"
                      id="indivi-return-input-slider-1"
                      oninput="slideOne1()"
                    />
                    <input
                      type="range"
                      min="0"
                      max="25"
                      value="25"
                      id="indivi-return-input-slider-2"
                      oninput="slideTwo1()"
                    />
                  </div>
                  <div class="return-input-values">
                    <input type="text" id="indivi-range1" />
                    <span>To</span>
                    <input type="text" id="indivi-range2" />
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="min-investment">
                  <h2>Minimum Investment</h2>
                  <div class="invest-range">
                    <div class="invest-amount-input">
                      <div
                        id="price"
                        class="invest-amount-bubble indivi-amount-bubble"
                      >
                        ₹ 1000
                      </div>
                      <input
                        type="range"
                        max="20000"
                        min="1000"
                        name="amount"
                        value="1000"
                        class="invest-input styled-slider slider-progress indivi-slider"
                      />
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="filter-risk">
                  <h2>Risk</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="low" />
                      <label for="low">Low</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="moderate" />
                      <label for="moderate">Moderate</label>
                    </div>
                    <div class="check-row">
                      <input
                        class="risk-checkbox"
                        type="checkbox"
                        name="moderate-high"
                      />
                      <label for="moderate-high">Moderately High</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="high" />
                      <label for="high">High</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="very-high" />
                      <label for="very-high">Very High</label>
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="filter-cri-rating">
                  <h2>Crisil Rating</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input type="radio" name="star-rating" id="" />
                      <label for="star-rating">
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Blankstar} alt="" />
                      </label>
                    </div>
                    <div class="check-row">
                      <input type="radio" name="star-rating" id="" />
                      <label for="star-rating">
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Blankstar} alt="" />
                            <img src={Blankstar} alt="" />
                      </label>
                    </div>
                    <div class="check-row">
                      <input type="radio" name="star-rating" id="" />
                      <label for="star-rating">
                            <img src={Stars} alt="" />
                            <img src={Stars} alt="" />
                            <img src={Blankstar} alt="" />
                            <img src={Blankstar} alt=""/>
                            <img src={Blankstar} alt="" />
                      </label>
                    </div>
                    <div class="check-row">
                      <input type="radio" name="star-rating" id="" />
                      <label for="star-rating">
                        <img src={Stars} alt="" />
                        <img src={Blankstar} alt="" />
                        <img src={Blankstar} alt="" />
                        <img src={Blankstar} alt="" />
                        <img src={Blankstar} alt="" />
                      </label>
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="filter-class">
                  <h2>Class</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input type="checkbox" name="nse-ind" />
                      <label for="nse-ind">NSE Index</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="bse-ind" />
                      <label for="bse-ind">BSE Index</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="world-ind" />
                      <label for="world-ind">World Index</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="debt" />
                      <label for="debt">Debt</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="gold" />
                      <label for="gold">Gold</label>
                    </div>
                  </div>
                </div>
              </div>
              <div class="etf-baskets-filters">
                <div class="percentage-ret">
                  <h2>Percentage Return (CAGR)</h2>
                  <div class="return-input-bars">
                    <div class="basket-return-input-slider-track"></div>
                    <input
                      type="range"
                      min="0"
                      max="25"
                      value="0"
                      id="basket-return-input-slider-1"
                      oninput="slideOne2()"
                    />
                    <input
                      type="range"
                      min="0"
                      max="25"
                      value="25"
                      id="basket-return-input-slider-2"
                      oninput="slideTwo2()"
                    />
                  </div>
                  <div class="return-input-values">
                    <input type="text" id="basket-range1" />
                    <span>To</span>
                    <input type="text" id="basket-range2" />
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="min-investment">
                  <h2>Minimum Investment</h2>
                  <div class="invest-range">
                    <div class="invest-amount-input">
                      <div
                        id="price"
                        class="invest-amount-bubble basket-amount-bubble"
                      >
                        ₹ 1000
                      </div>
                      <input
                        type="range"
                        max="20000"
                        min="1000"
                        name="amount"
                        value="1000"
                        class="invest-input styled-slider slider-progress indivi-slider basket-slider"
                      />
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="filter-risk">
                  <h2>Risk</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="low" />
                      <label for="low">Low</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="moderate" />
                      <label for="moderate">Moderate</label>
                    </div>
                    <div class="check-row">
                      <input
                        class="risk-checkbox"
                        type="checkbox"
                        name="moderate-high"
                      />
                      <label for="moderate-high">Moderately High</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="high" />
                      <label for="high">High</label>
                    </div>
                    <div class="check-row">
                      <input class="risk-checkbox" type="checkbox" name="very-high" />
                      <label for="very-high">Very High</label>
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="filter-market-cap">
                  <h2>Market Cap</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input type="checkbox" name="multi-cap" />
                      <label for="multi-cap">Multi Cap</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="large-cap" />
                      <label for="large-cap">Large Cap</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="med-cap" />
                      <label for="med-cap">Medium Cap</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="small-cap" />
                      <label for="small-cap">Small Cap</label>
                    </div>
                  </div>
                </div>
                <hr class="filters-hr" />
                <div class="platform">
                  <h2>Platform</h2>
                  <div class="checkboxes">
                    <div class="check-row">
                      <input type="checkbox" name="wealth-desk" />
                      <label for="wealth-desk">Wealth Desk</label>
                    </div>
                    <div class="check-row">
                      <input type="checkbox" name="smallcase" />
                      <label for="smallcase">Smallcase</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>;
};

export default ExplorePageSideContent;
