import React, {useState} from 'react'
import Dropdown from '../../assets/images/drop-down.svg'

const FaqComponent = (props) => {
    const {listfaq} = props
    const [isOpen,setIsopen] = useState(false)
    const [curQus,setCurqus] = useState(null)
    const handleToggle = (index) =>{
    //   setIsopen(!isOpen)
      if(curQus !== index){
          setCurqus(index)
          setIsopen(true)
      }else{
          setIsopen(!isOpen)
      }
    }
  return (
      
    <div  className="accordion" style={{height : (curQus === listfaq.id && isOpen) && 'auto',transition: 'height .4s ease-in-out'}} onClick={() =>handleToggle(listfaq.id)}>
              <div className="accordion-head">
                <span className="drop-down">
                  <img src={Dropdown} alt="" style={{transform: isOpen && 'rotate(180deg)'}}/>
                </span>
                <h1>{listfaq.qus}</h1>
              </div>
              <p style={{height: isOpen && 'auto'}}>
                {listfaq.ans}
              </p>
    </div>
  )
}

export default FaqComponent