import React from 'react'
import {Link} from 'react-router-dom'
import Horses from '../../assets/images/horse.svg'
const ETFCard = (props) => {
    const {nav, returns, fund_details} = props
    // console.log(props.data.fund_details.FUND_NAME)
  return (
    <div className="card">
                <Link to={`/details/${props.data.fund_details.SCH_CODE}`}>
                  <div className="card-info">
                    <div className="nifty-sensex-text">
                      <h1 className="light-heading">Nifth Midcap 150</h1>
                    </div>
                    <p>1Y Returns</p>
                    <h1 className="main-heading">
                    {(String(props.data.returns).charAt(0) !== '-') && '+'}
                    {Number(props.data.returns).toFixed(2)}%</h1>
                  </div>
                  <img className="vector-img horse" src={Horses} alt=""/>
                  <div className="card-bg">
                    <div className="card-img"></div>
                  </div>
                  <div className="card-details">
                    {/* <p>ICICI Pru Nifty <br/>Midcap 150 ETF</p> */}
                    <p>{props.data.fund_details.FUND_NAME}</p>
                    <p><span>NAV</span>₹{Number(props.data.nav).toFixed(2)}</p>
                  </div>
                </Link>
              </div>
  )
}

export default ETFCard