import React, {useState,useEffect, Fragment} from 'react'
import axios from 'axios'
import {Link, useHistory} from 'react-router-dom'
import {EtfList}from './etfList'
import ETFCard from '../etfcards/ETFCard'
import SearchIcon from '../../assets/images/search.svg'
// import Horses from '../../assets/images/horse.svg'
// import PiggyIcon from '../../assets/images/Piggy Bank.svg'
// import FlowerIcon from '../../assets/images/Flower.svg'
import Leftarrow from '../../assets/images/arrow-left.svg'
import Rightarrow from '../../assets/images/arrow-right.svg'
import CrossIcon from '../../assets/images/cross-icon.svg'
// import PrimarySpinner from '../spinners/PrimarySpinner'
import Secondaryspinner from '../spinners/Secondaryspinner'


const FindETF = () => {
    const [currentCategory,setCurrentcategory] = useState('new-launches')
    const [isDesktopsearchopen,setIsdesktopsearchopen] = useState(false)
    const [desktopinput,setDesktopinput] = useState('')
    const [desktopsearchresult,setDesktopsearchresult] = useState([])
    const [isMobilesearchopen,setIsmobilesearchopen] = useState(false)
    const [mobileinput,setMobileInput] = useState('ICICI Prudential')
    const [mobilesearchresult,setMobilesearchresult] = useState([])
    const categoryList = ['new-launches', 'first-time', 'safe-bet', 'better-mfs']
    const [listOfETF,setListofetf] = useState([])
    const [randomCards,setRandomcards] = useState([])
    const [searchCount,setSearchcount] = useState(0)
    const history = useHistory()
    useEffect(()=>{
      window.scrollTo(0, 0)
      fetchAllList()
    // eslint-disable-next-line no-undef  
    },[])
    const handleLeftArrows = () =>{
      let cards = document.querySelectorAll(".container .card");
      let leftArrow = document.querySelector(
        "#find-the-etf .find-the-etf-cta .arrows span:first-child"
      );

      leftArrow.addEventListener("click", () => {
        cards.forEach((card) => {
          card.style.left = "0rem";
        });
      });
    }
    const handleRightArrows = () =>{
      let cards = document.querySelectorAll(".container .card");
      let rightArrow = document.querySelector("#find-the-etf .find-the-etf-cta .arrows span:last-child");

      rightArrow.addEventListener("click", () => {
        cards.forEach((card) => {
          card.style.left = "-48rem";
        });
      });
    }
    const handleCategory = (index) =>{
      setCurrentcategory(categoryList[index])
    }
    const handleDesktopSearch = () =>{
      setIsdesktopsearchopen(true)
      if(desktopinput.length <=17){
        setDesktopinput('ICICI Prudential ')
      }
      
    }
    const handleDesktopInput = (e) =>{ 
      // console.log(e.target.value)
      if(String(e.target.value).length >=17){
        setDesktopinput(e.target.value)
        handleDesktopTextSearch(e.target.value)
      }else{
        setDesktopinput('ICICI Prudential ')
      }
        
    }
    const handleDesktopTextSearch =(value)=>{
      let list = []
      // let finalSix = []
      for(let i=0; i < EtfList.length;i++){
        if(EtfList[i].etfname.toLowerCase().indexOf(value.toLowerCase()) > -1){
            list.push(EtfList[i])
            
        }
      }
      // for(let j=0; j< list.length;j++){
      //   finalSix.push(list[j])
      // }
      // console.log(finalSix)
      setDesktopsearchresult(list)
    }
    const handleCrossDesktop =(event) =>{
      event.stopPropagation()
      setIsdesktopsearchopen(false)
      setDesktopinput('')
      setDesktopsearchresult([])
      setSearchcount(0)
    }
    const handleMobileSearch = () =>{
      setIsmobilesearchopen(true)
    }
    const handleMobileInput =(e)=>{
      setMobileInput(e.target.value)
      handleMobileTextSearch(e.target.value)
    }
    const handleCrossMobile = () =>{
      setIsmobilesearchopen(false)
      setMobileInput('ICICI Pru')
    }
    const clearFields = (screens) =>{
      if(screens === 'mobile'){
        setMobileInput('ICICI Pru')
      }else{
        setDesktopinput('')
      }
    }
    const handleMobileTextSearch = (value) =>{
      console.log(searchCount)
      let list = []
      for(let i=0; i < EtfList.length;i++){
        if(EtfList[i].etfname.toLowerCase().indexOf(value.toLowerCase()) > -1){
            list.push(EtfList[i])
            
        }
      }
      setMobilesearchresult(list)
    }
    const handleDesktopInputKeyDown =(e)=>{
     
      if(e.key === 'ArrowDown'){
        console.log(desktopsearchresult,searchCount) 
        
        if(searchCount >=4 || searchCount >= desktopsearchresult.length){
          setSearchcount(0)
        }else{
          if(desktopsearchresult.length !==0) {
            let resultcontainer = document.getElementById('etf-searchbox-desktop-result-c')
            if(searchCount >= desktopsearchresult.length-1){
              setDesktopinput(desktopsearchresult[searchCount].etfname)
               setSearchcount(searchCount - 0)
               console.log(searchCount)
              //  console.log(resultcontainer)
             }else{
              setDesktopinput(desktopsearchresult[searchCount].etfname)
              setSearchcount(searchCount + 1)
              console.log(searchCount, desktopsearchresult)
              let curElem = resultcontainer.getElementsByTagName('li')
              // curElem[searchCount].style.backgroundColor = '#333'
              // let ptag = atag.
              // atag.style = '#204181'
              // resultcontainer.
              // for(let i=0; i< curElem.length;i++){
              //   if(curElem[i] === searchCount){
              //     curElem[i].style.backgroundColor = '#204181'
              //   }
                
              // }
              // console.log(curElem[searchCount])
             }
          }  
        }
        
         
     
      }

      // if(e.key === 'ArrowUp'){
            
      //     if(desktopsearchresult.length !==0) {
      //       if(searchCount >= desktopsearchresult.length-1){
      //         setDesktopinput(desktopsearchresult[searchCount].etfname)
      //          setSearchcount(searchCount - 1)
      //          console.log(searchCount)
      //        }else{
      //         setDesktopinput(desktopsearchresult[searchCount].etfname)
      //         setSearchcount(searchCount + 1)
      //         console.log(searchCount, desktopsearchresult)
      //        }
      //     }

      //   }
      if(e.key === 'Enter'){
        console.log(desktopinput);
        for(let i=0;i<desktopsearchresult.length;i++){
          if(desktopinput === desktopsearchresult[i].etfname){
            history.push(`/details/${desktopsearchresult[i].etfschcode}`)
          }
        }
        
      }
    }
    const handleList = () =>{
      axios.post('/api2/GetFundMasterByCategory',
      {"request":""}
      )
      .then(res =>{
        let result =[];
        let uniqueRes =[]
        // console.log(JSON.parse(res.data.Data).Table)
        // console.log(res)
        result = JSON.parse(res.data.Data).Table
        for(let i=0; i<result.length; i++){
          if(uniqueRes.indexOf(result[i].FUND_CODE) === -1){
            uniqueRes.push(result[i])
          }
        }
        
        let clean = uniqueRes.filter((arr, index, self) =>
            index === self.findIndex((t) => (t.FUND_CODE === arr.FUND_CODE)))
        // console.log(clean)
        // setListofetf(clean)
        combineData(clean)
        
      })
      .catch(err => console.log(err))
    }

    const fetchAllList = () =>{
      handleList()
      
    }
    const combineData = (sortData) =>{
      let finalRes = []
     
      const urls =['/api1/LandingPage/','/api1/Performance_Default/']
      const fetchUrl = (url,fundCode) =>{
        return axios.post(`${url}${fundCode}`,
      {"request":""}
      )
      }
      for(let i=0;i < sortData.length;i++){
        let obj = {}
    
        // for returns details
        const promiseArr = urls.map(url => fetchUrl(url,sortData[i].SCH_CODE))
        Promise.all(promiseArr)
        .then((data) =>{
          // we are not taking 4 index now because data from api is not consistent. While in Uat remove this condition 
          if(i !== 4){
            obj.nav = data[0].data[0].NAV
            obj.returns = data[1].data[1].Year_1
            obj.fund_details = sortData[i]
            finalRes.push(obj)
            // console.log(finalRes)
            

          }
          setListofetf(finalRes)
          
          
        }).finally(() =>{finalRes.length > 11 && RandomSixBeaches(finalRes)})
        

        // setRandomcards(RandomSixBeaches(listOfETF))
        // let returnRes = fetchUrl(urls[1],sortData[i].SCH_CODE)
        // returnRes
        // .then(res =>{
        //   if(i !== 14 && i !== 18){
        //     console.log('returns',res.data[1].Year_1)
        //     obj.returns = res.data[1].Year_1
        //     obj.fund_name = sortData[i].FUND_NAME
        //     finalRes.push(obj)
        //     console.log(finalRes)
        //   }
          
        // })
        // .catch(err => console.log(err))
       
      }
    }

    const RandomSixBeaches = (data) =>{
      let randomSixNumber = []
      let randomSixCard = []
      function randomIntFromInterval(min, max) { // min and max included 
        return Math.floor(Math.random() * (max - min + 1) + min)
      }
      // if(data.length >= 7){
        
        // this while loop saved me . please reconsider if want to destroy the code
        let j=6
        while(j !== 0){
          let randNum = randomIntFromInterval(0,data.length-1)
          if(randomSixNumber.includes(randNum) === false){
            randomSixNumber.push(randNum)
            j--
          }
        }
        for(let j=0;j< randomSixNumber.length;j++){
          randomSixCard.push(data[randomSixNumber[j]])
        }
      // }
      // console.log(randomSixCard)
      // return random Six Card
      setRandomcards(randomSixCard)
    }
    
    return (
        <section id="find-the-etf">
          <div className="find-the-etf">
            <h1 onClick={handleList}>Find the right ETF for you</h1>
          <div className="find-the-etf-navbar">
            <ul>
              <img id="search-option" className="search-option" onClick={handleMobileSearch} src={SearchIcon} alt=""/>
              <li 
                style={{backgroundColor: (currentCategory === 'new-launches')  && 'rgb(246,130,31)', color: (currentCategory === 'new-launches') && 'rgb(255,255,255)'}}
                onClick={() => handleCategory(0)}
                >New Launches</li>
              <li
                style={{backgroundColor: (currentCategory === 'first-time')  && 'rgb(246,130,31)', color: (currentCategory === 'first-time') && 'rgb(255,255,255)'}}
                onClick={() => handleCategory(1)}
                >1st Time Investment</li>
              <li
                style={{backgroundColor: (currentCategory === 'safe-bet')  && 'rgb(246,130,31)', color: (currentCategory === 'safe-bet') && 'rgb(255,255,255)'}}
                onClick={() => handleCategory(2)}
                >Safe Bets</li>
              <li
                style={{backgroundColor: (currentCategory === 'better-mfs')  && 'rgb(246,130,31)', color: (currentCategory === 'better-mfs') && 'rgb(255,255,255)'}}
                onClick={() => handleCategory(3)}
                >Better than MFs</li>
              <li id="etf-searchbox-container" onClick={handleDesktopSearch} style={{width : isDesktopsearchopen && '92.5%',border: isDesktopsearchopen && '1px solid rgb(233,233,233)'}}>
                <img src={SearchIcon} alt=""/>
                <input 
                    type="text" 
                    name="" 
                    id="etf-searchbox-desktop-input" 
                    placeholder="Search for ETFs"
                    value={desktopinput}
                    autoComplete="off"
                    style={{textTransform: "capitalize"}}
                    onChange={handleDesktopInput}
                    onKeyDown={handleDesktopInputKeyDown}

                />
                  <img src={CrossIcon} alt="" id="close-search-btn" onClick={handleCrossDesktop} style={{display : (isDesktopsearchopen === true) ? 'block' : 'none'}}/>
                <ul id="etf-searchbox-desktop-result-c" className='etf-searchbox-desktop-result-c' style={{display : (isDesktopsearchopen === true) ? 'block' : 'none'}}>
                  {desktopsearchresult.length >=16 ?
                  desktopsearchresult.length < 1 ?
                  <>
                  <li className='etf-searchbox-desktop-result-item'>
                    <Link to='/details/NV20'><p>No ETF Found.</p></Link>
                  </li>
                  </>
                  :
                  <>
                  <li className='etf-searchbox-desktop-result-item'>
                    <Link to='/details/5010'><p>ICICI Prudential Midcap 150 ETF</p></Link>
                  </li>
                  <li className='etf-searchbox-desktop-result-item'>
                    <Link to='/details/SPICE'><p>ICICI Prudential Sensex ETF</p></Link>
                  </li>
                  <li className='etf-searchbox-desktop-result-item'>
                    <Link to='/details/5006F'><p>ICICI Prudential Nifty Next 50 ETF</p></Link>
                    </li>
                    <li className='etf-searchbox-desktop-result-item'>
                    <Link to='/details/5000'><p>ICICI Prudential Nifty 100 ETF</p></Link>
                    </li>
                  </>  
                :
                desktopsearchresult.map((list,index)=>(
                 <Fragment key={index}>
                 <li className='etf-searchbox-desktop-result-item'>
                <Link to={`/details/${list.etfschcode}`}><p id="etf-result-name">{list.etfname}</p></Link>
                </li>
                </Fragment>
                ))
                }
                  
                  
                </ul>
              </li>
            </ul>
          </div>
          <div className="container">

            {/* final cards */}
           {
          
           (randomCards.length >=6) ?
           randomCards.map((card,index)=>(
             <ETFCard data={card} key={index} />
           ))
           :
           <>
           <Secondaryspinner />
           <Secondaryspinner />
           <Secondaryspinner />
           <Secondaryspinner />
           <Secondaryspinner />
           <Secondaryspinner />
           </>
          }
            
             

            
          </div>
          <div className="find-the-etf-cta">
              <Link to='/explore'>Explore All</Link>
              <div className="arrows">
                <span onClick={handleLeftArrows}><img src={Leftarrow} alt=""/></span>
                <span onClick={handleRightArrows}><img src={Rightarrow} alt=""/></span>
              </div>
            </div>
          </div>
          {/* mobile search  */}
          <div id="find-etf-mobile-search-container" style={{display: isMobilesearchopen && 'flex'}}>
            <div className="find-etf-mobile-searchbox">
              <img id="etf-mobile-search-back" src={Leftarrow} alt="back" onClick={handleCrossMobile}/>
              <input 
                id="etf-mobile-search-box-input" 
                type="text" 
                placeholder="Search for ETF's" 
                autoComplete="off" 
                value={mobileinput} 
                onChange={handleMobileInput}
                style={{textTransform: "capitalize"}}
              />
              <img id="etf-mobile-search-clear" src={CrossIcon} alt="close" onClick={() => clearFields('mobile')} style={{visibility: "visible"}}/>
            </div>
            {mobileinput.length <=9 ?
              <div className="find-etf-mobile-searchbox-results">
            <p>
                <Link to='/details/5010'>
                <img src={SearchIcon} alt="search"/>
                <span style={{color: "rgb(32, 65, 129)"}}>ICICI Prudential Midcap 150 ETF </span>
                </Link>
              </p>
              <p>
                <Link to='/details/SPICE'>
                <img src={SearchIcon} alt="search"/>
                <span style={{color: "rgb(32, 65, 129)"}}>ICICI Prudential</span> Sensex ETF
                </Link>
              </p>
              <p>
                <Link to='/details/5006F'>
                <img src={SearchIcon} alt="search"/>
                <span style={{color: "rgb(32, 65, 129)"}}>ICICI Prudential</span> Nifty Next 50 ETF
                </Link>
              </p>
              <p>
                <Link o='/details/5000'>
                <img src={SearchIcon} alt="search"/>
                <span style={{color: "rgb(32, 65, 129)"}}>ICICI Prudential</span> Nifty 100 ETF
                </Link>
              </p>
              </div>
            :
            <div className="find-etf-mobile-searchbox-results">
            {mobilesearchresult.length !==0 && 
            mobilesearchresult.map((etf,index) =>{
              return (
                <p key={etf.id} style={{backgroundColor : index === 0 && '#f2f6fa'}}>
                <Link to={`/details/${etf.etfschcode}`}>
                <img src={SearchIcon} alt="search"/>
                <span style={{color: index === 0 && 'rgb(32, 65, 129)'}}>{etf.etfname}</span>
                </Link>
            </p>
              )
            })
            
            }
            
            </div>
            }
          </div>
      </section>
    )
}

export default FindETF
