import React,{useEffect,useState} from 'react';
import axios from 'axios';
import {useParams} from 'react-router-dom'
import PrimaryNavbar from '../navbars/PrimaryNavbar';
import DetailsPageMainContent from './main/DetailsPageMainContent';
import DetailsPageSideContent from './side/DetailsPageSideContent';
import News from '../homepage/News'
// import Daily from '../homepage/Daily'
import HomeFooter from '../homepage/HomeFooter'
import Logo from '../../assets/images/logo.svg'
import News1 from '../../assets/images/news-01.svg'
import News2 from '../../assets/images/news-02.svg'
import News3 from '../../assets/images/news-03.svg'
// import Spinner from '../../assets/images/spinners/primary-spinner.svg'
import './css/DetailsPage.css'
import { EtfList } from '../homepage/etfList';
import PrimarySpinner from '../spinners/PrimarySpinner';
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
const DetailsPage = () => {
  const {fcode} = useParams()
  console.log(fcode)
  const [listOfETF,setListofetf] = useState([])
  const [currentETF,setCurrentetf] = useState({})
  const [isLoading,setIsloading] = useState(false)
  useEffect(() =>{
     window.scrollTo(0, 0)
     fetchAllList()
  },[])

  // GET all ETF's List
  const handleList = () =>{
    axios.post('/api2/GetFundMasterByCategory',
    {"request":""}
    )
    .then(res =>{
      let result =[];
      let uniqueRes =[]
      console.log(JSON.parse(res.data.Data).Table)
      // console.log(res)
      result = JSON.parse(res.data.Data).Table
      for(let i=0; i<result.length; i++){
        if(uniqueRes.indexOf(result[i].FUND_CODE) === -1){
          uniqueRes.push(result[i])
        }
      }

      let clean = uniqueRes.filter((arr, index, self) =>
          index === self.findIndex((t) => (t.FUND_CODE === arr.FUND_CODE)))
      console.log(clean)
      // setListofetf(clean)
      combineData(clean)

    })
    .catch(err => console.log(err))
  }

  // To Call when page loads
  const fetchAllList = () =>{
    handleList()
    // findCurrentETF(fcode)
  }

  const combineData = (sortData) =>{
    let finalRes = []

    const urls =['/api1/LandingPage/','/api1/Performance_Default/','/api1/Fund_Details/']
    const fetchUrl = (url,fundCode) =>{
      return axios.post(`${url}${fundCode}`,
    {"request":""}
    )
    }
    for(let i=0;i < sortData.length;i++){
      let obj = {}

      // for returns details
      const promiseArr = urls.map(url => fetchUrl(url,sortData[i].SCH_CODE))
      Promise.all(promiseArr)
      .then((data) =>{
        if(i !== 4){
          obj.nav = data[0].data[0].NAV
          obj.returns = data[1].data[1].Year_1
          obj.fund_details = sortData[i]
          obj.fund_details_extra = data[2]
          obj.invest_horizon = data[2].data[2].Data
          obj.aum = data[2].data[4].Data
          obj.schcode = sortData[i].SCH_CODE
          obj.fundcode = sortData[i].FUND_CODE
          obj.fundname = sortData[i].FUND_NAME

          finalRes.push(obj)
          console.log(finalRes)
          if(fcode === sortData[i].SCH_CODE){
            findCurrentETF(obj)
            console.log(obj)
            setIsloading(false)
          }

        }
        // setListofetf(finalRes)


      })
      .finally(() => {
        setListofetf(finalRes)

        console.log(listOfETF)
      })
      .catch(err => console.log(err))

      // setRandomcards(RandomSixBeaches(listOfETF))
      // let returnRes = fetchUrl(urls[1],sortData[i].SCH_CODE)
      // returnRes
      // .then(res =>{
      //   if(i !== 14 && i !== 18){
      //     console.log('returns',res.data[1].Year_1)
      //     obj.returns = res.data[1].Year_1
      //     obj.fund_name = sortData[i].FUND_NAME
      //     finalRes.push(obj)
      //     console.log(finalRes)
      //   }

      // })
      // .catch(err => console.log(err))

    }
  }
  const findCurrentETF = (etf) =>{
    // for(let i=0; i< listOfETF.length;i++){
      // if(listOfETF[i].schcode === schcode){
        setCurrentetf(etf)
        // console.log(listOfETF[i])
      // }
    // }
  }
  return (
    <>
      <PrimaryNavbar />
      {isLoading ? (
        <PrimarySpinner />
      ) : (
        <>
          <section id="main">
            <DetailsPageMainContent data={currentETF} />
            <DetailsPageSideContent data={currentETF} />
          </section>
          <section id="details-news">
            <div class="details-news display-none-in-mobile">
              <div class="d-news-heading">
                <h1>{`News & Insights`}</h1>
                <a href="#">View More</a>
              </div>
              <div class="d-news-container">
                <div class="d-news-card">
                  <img style={{ borderRadius: "1rem" }} src={News1} alt="" />
                  <p class="d-news-date">April 02, 2021</p>
                  <p class="d-news-desc">
                    Why ETFs are better than your <br />
                    MF investments?
                  </p>
                </div>
                <div class="d-news-card">
                  <img src={News2} alt="" />
                  <p class="d-news-date">March 26, 2021</p>
                  <p class="d-news-desc">
                    Where should I invest, Gold ETF <br />
                    vs Gold Bond?
                  </p>
                </div>
                <div class="d-news-card">
                  <img src={News3} alt="" />
                  <p class="d-news-date">March 19, 2021</p>
                  <p class="d-news-desc">
                    10 Things to know before your <br />
                    invest in any ETF
                  </p>
                </div>
              </div>
            </div>

            <div class="details-news display-none-in-dekstop">
              <div class="d-news-heading">
                <h1>{`News & Insights`}</h1>
              </div>
              <div class="d-news-container">
                <OwlCarousel
                  className="owl-theme"
                  stagePadding={50}
                  margin={20}
                  items={1}
                  nav
                >
                  <div className="item">
                    <div class="d-news-card">
                      <img
                        style={{ borderRadius: "1rem" }}
                        src={News1}
                        alt=""
                      />
                      <p class="d-news-date">April 02, 2021</p>
                      <p class="d-news-desc">
                        Why ETFs are better than
                        <br />
                        your MF investments?
                      </p>
                    </div>
                  </div>
                  <div className="item">
                    <div class="d-news-card">
                      <img src={News2} alt="" />
                      <p class="d-news-date">March 26, 2021</p>
                      <p class="d-news-desc">
                        Where should I invest, Gold
                        <br />
                        ETF vs Gold Bond?
                      </p>
                    </div>
                  </div>
                  <div className="item">
                    <div class="d-news-card">
                      <img src={News3} alt="" />
                      <p class="d-news-date">March 19, 2021</p>
                      <p class="d-news-desc">
                        10 Things to know before your <br />
                        invest in any ETF
                      </p>
                    </div>
                  </div>
                </OwlCarousel>
                <div className="details-news-view-more-mobile">
                  <a href="#">View More</a>
                </div>
              </div>
            </div>
          </section>
          <section id="details-footer">
            <footer id="d-footer">
              <div class="d-footer-logo">
                <img src={Logo} alt="" />
                <p>© ICICI Pru AMC</p>
              </div>
              <ul class="d-footer-links">
                <li>
                  <a href="#">Explore</a>
                </li>
                <li>
                  <a href="#">Knowledge Center</a>
                </li>
                <li>
                  <a href="#">About</a>
                </li>
                <li>
                  <a href="#">Contact</a>
                </li>
              </ul>
            </footer>
          </section>
          {/* <News />
            <HomeFooter /> */}
        </>
      )}
    </>
  );
};

export default DetailsPage;
