import React from 'react'
import { Link as ScrollLink } from "react-scroll";
// import scrollToComponent from 'react-scroll-to-component';
const HomeNavbarSecondary = () => {
    // let stickyNav = useRef()
    // console.log(stickyNav.current)
    // var currentScrollPos = window.pageYOffset;
    // let secNavbarTop = document.getElementById('navbar-2')
    // var currentScrollPos = window.pageYOffset;
    


    window.addEventListener('scroll',()=>{
      const navLi = document.querySelectorAll("#navbar-2 ul li a");
      const sections = document.querySelectorAll("section");
      // console.log(navLi)
      // console.log(sections)
      // console.log(window.scrollY)
      let secNavbarTop = document.getElementById("navbar-2")
      let stickyNav = document.getElementById("sticky-navbar") 
      let whyInvest = document.getElementById("why-to-invest")
      let primaryNav = document.getElementById("primary-navbar").style.top
      // console.log('primary navbar', primaryNav)
      // if(primaryNav === -100){
      //   secNavbarTop.style.top = '70px'
      // }
      if(secNavbarTop &&  secNavbarTop.getBoundingClientRect().top <= 70){
        // console.log('sticky', secNavbarTop.getBoundingClientRect().top)
        secNavbarTop.style.position = 'fixed'
        secNavbarTop.style.top = primaryNav === '0px' ? '70px' : '0px'
        secNavbarTop.style.width = '85.1%'
        stickyNav.style.backgroundColor = '#204181';
        stickyNav.style.borderRadius = '0 0 1.6rem 1.6rem'
        secNavbarTop.style.borderRadius = '0 0 1.6rem 1.6rem'

      }
      // console.log(whyInvest.getBoundingClientRect().top)
      if(whyInvest && whyInvest.getBoundingClientRect().top > 100){
        // console.log('sticky', secNavbarTop.getBoundingClientRect().top)
        // console.log('unstick')
        secNavbarTop.style.position = 'absolute'
        secNavbarTop.style.top = 'unset'
        secNavbarTop.style.width  ='100%'
        stickyNav.style.backgroundColor = '#ffffff1a'
        stickyNav.style.borderRadius = '1.6rem 1.6rem 0 0'
        secNavbarTop.style.borderRadius = '1.6rem 1.6rem 0 0'
        
    // border-radius: 0 0 16px 16px!important

      }

      // links
      let current = "";
  sections.forEach((section) => {
    const sectionTop = section.offsetTop;
    const sectionHeight = section.clientHeight;
    if (window.scrollY >= sectionTop - sectionHeight / 2) {
      current = section.getAttribute("id");
    }
  });

  if (current === "" || current === "why-to-invest") {
    navLi[0].classList.add("active-option");
    for (let i = 1; i < navLi.length; i++) {
      navLi[i].classList.remove("active-option");
    }
  } else {
    navLi.forEach((li) => {
      li.classList.remove("active-option");
      if (li.classList.contains(current + "-link")) {
        li.classList.add("active-option");
      }
    });
  }
      
    })
    // TO MAKE LINKS ACTIVE
    // const handleLinks = (index) =>{
    //   let navlist = document.getElementById("sticky-navbar")
    //   for(let i=0; i< navlist.length;i++){

    //   }
    //   // console.log(navlist.childNodes[index].firstChild.classList)
    // }
    return (
      <div id="navbar-2" className="sticky">
        <ul id="sticky-navbar">
          <li>
            <ScrollLink 
              activeClass="active"
              to="why-to-invest"
              spy={true}
              smooth={true}
              offset={-100}
              duration={1000} 
              className="why-to-invest-link" href="#why-to-invest" rel="noreferrer noopener">Why to invest?</ScrollLink>
          </li>
          <li>
            <ScrollLink
              activeClass="active"
              to="find-the-etf"
              spy={true}
              smooth={true}
              offset={40}
              duration={1000} 
              className="find-the-etf-link" href="#find-the-etf" rel="noreferrer noopener">Find Right ETFs</ScrollLink>
          </li>
          <li>
            <ScrollLink
              activeClass="active"
              to="how-to-choose"
              smooth={true}
              
              duration={1000} 
             className="how-to-choose-link" href="#how-to-choose" rel="noreferrer noopener">How to choose?</ScrollLink>
          </li>
          <li>
            <ScrollLink 
              activeClass="active"
              to="how-to-buy-sell-card"
              smooth={true}
              offset={-100}
              duration={1000} 
              className="how-to-buy-link" href="#how-to-buy" rel="noreferrer noopener">How to Buy/Sell?</ScrollLink>
          </li>
          <li>
            <ScrollLink 
              activeClass="active"
              to="why-you-should-buy-wtf"
              smooth={true}
              offset={-100}
              duration={1000} 
              className="why-should-buy-link" href="#why-should-buy" rel="noreferrer noopener">Why us?</ScrollLink>
          </li>
          <li>
          <ScrollLink 
            activeClass="active"
            to="faq"
            smooth={true}
              
            duration={1000} 
            className="faq-link" href="#faq" rel="noreferrer noopener">FAQs</ScrollLink>
          </li>
        </ul>
      </div>
    )
}

export default HomeNavbarSecondary
