import React, {useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import IciciLogo from '../../assets/images/image 48.svg'
import Hamburger from '../../assets/images/hamburger.svg'
const HomeNavbarPrimary = () => {
  const [currentWindow,setCurrentwindow] = useState(true)
  var prevScrollpos = window.pageYOffset;
  
  window.addEventListener('scroll',()=>{
    // console.log(window.location.pathname)
    // weather to show primary navbar 
    if(window.location.pathname !== '/'){
      setCurrentwindow(false)
    }else{
      setCurrentwindow(true)
    }
    var currentScrollPos = window.pageYOffset;
    // MAKE NAVBAR HIDE/SHOW ON SCROLL. IF SCROLLS UP: SHOW . IF SCROLLS DOWN: HIDE
    if (prevScrollpos > currentScrollPos) {
      document.getElementById("primary-navbar").style.top = "0";
    } else {
      document.getElementById("primary-navbar").style.top = "-100px";
    }

    prevScrollpos = currentScrollPos;
    // console.log('hello navbar', currentScrollPos)
    })
   
  
    
    // window.addEventListener('load',() =>{
    //   if(window.location.pathname !== '/'){
    //     setCurrentwindow(false)
    //   }else{
    //     setCurrentwindow(true)
    //   }
      
    // })
    // useEffect(() =>{
    //   if(window.location.pathname !== '/'){
    //     setCurrentwindow(false)
    //   }else{
    //     setCurrentwindow(true)
    //   }
    // },[])

  
    return (
      <div className="navbar" id="primary-navbar" style={{display: (currentWindow === false) && 'none'}}>
      <Link to='/'><img src={IciciLogo} className="logo" alt=""/></Link>
      <nav id="homenavprimary">
        <ul className="first-nav-options">
          <li><Link to='/explore'>Explore</Link></li>
          <li><a href="https://www.icicipruamc.com/about-us" target="_blank" rel="noreferrer openner">About Us</a></li>
          <li><a href="https://www.icicipruamc.com/help-and-support/contact-us" target="_blank" rel="noreferrer openner">Contact</a></li>
        </ul>
      </nav>
      <img src={Hamburger} className="hamburger" alt=""/>
      </div>
    )
}

export default HomeNavbarPrimary
