import React,{useEffect} from 'react';
import '../css/DetailsPageSide.css'
import RiskometerImg from '../../../assets/images/riskometer.svg'
import DownloadBtn from '../../../assets/images/download-button.svg'
import InfoBtn from '../../../assets/images/info-button.svg'
const DetailsPageSideContent = (props) => {
 useEffect(()=>{
   // Custom Dropdown
var x, i, j, l, ll, selElmnt, a, b, c;
x = document.getElementsByClassName("custom-select");
l = x.length;
for (i = 0; i < l; i++) {
  selElmnt = x[i].getElementsByTagName("select")[0];
  ll = selElmnt.length;
  a = document.createElement("DIV");
  a.setAttribute("class", "select-selected");
  a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
  x[i].appendChild(a);
  b = document.createElement("DIV");
  b.setAttribute("class", "select-items select-hide");
  for (j = 1; j < ll; j++) {
    c = document.createElement("DIV");
    c.innerHTML = selElmnt.options[j].innerHTML;
    c.addEventListener("click", function (e) {
      var y, i, k, s, h, sl, yl;
      s = this.parentNode.parentNode.getElementsByTagName("select")[0];
      sl = s.length;
      h = this.parentNode.previousSibling;
      for (i = 0; i < sl; i++) {
        if (s.options[i].innerHTML === this.innerHTML) {
          s.selectedIndex = i;
          h.innerHTML = this.innerHTML;
          y = this.parentNode.getElementsByClassName("same-as-selected");
          yl = y.length;
          for (k = 0; k < yl; k++) {
            y[k].removeAttribute("class");
          }
          this.setAttribute("class", "same-as-selected");
          break;
        }
      }
      h.click();
    });
    b.appendChild(c);
  }
  x[i].appendChild(b);
  a.addEventListener("click", function (e) {
    e.stopPropagation();
    closeAllSelect(this);
    this.nextSibling.classList.toggle("select-hide");
    this.classList.toggle("select-arrow-active");
  });
}
function closeAllSelect(elmnt) {
  var x,
    y,
    i,
    xl,
    yl,
    arrNo = [];
  x = document.getElementsByClassName("select-items");
  y = document.getElementsByClassName("select-selected");
  xl = x.length;
  yl = y.length;
  for (i = 0; i < yl; i++) {
    if (elmnt === y[i]) {
      arrNo.push(i);
    } else {
      y[i].classList.remove("select-arrow-active");
    }
  }
  for (i = 0; i < xl; i++) {
    if (arrNo.indexOf(i)) {
      x[i].classList.add("select-hide");
    }
  }
}
/*if the user clicks anywhere outside the select box,
then close all select boxes:*/
document.addEventListener("click", closeAllSelect);

// Info text Toggling
let readMoreContent = document.querySelector(
  ".etf-info-body .read-more-content"
);
let readMoreDots = document.querySelector(".etf-info-body .points:first-child");
let readMoreButton = document.querySelector(".etf-info-body .readMore");
let viewLessButton = document.querySelector(".etf-info-body .viewLess");

readMoreButton.addEventListener("click", () => {
  readMoreContent.style.display = "inline";
  readMoreButton.style.display = "none";
  readMoreDots.style.display = "none";
});

viewLessButton.addEventListener("click", () => {
  readMoreContent.style.display = "none";
  readMoreButton.style.display = "inline";
  readMoreDots.style.display = "inline";
});

// Risk Toggler
let toggle = 1;
let riskToggler = document.querySelector(
  "#side-content .riskometer .risk-toggle "
);
let riskTogglerSwitch = document.querySelector(
  "#side-content .risk-toggle .switch"
);
riskToggler.addEventListener("click", () => {
  if (toggle === 1) {
    riskTogglerSwitch.style.color = "#f6821f";
    riskTogglerSwitch.style.left = "12.5rem";
    setTimeout(()=>{
      riskTogglerSwitch.innerText = "Benchmark";
      riskTogglerSwitch.style.color = "#fff";
    },1000)
    toggle = 0;
  } else {
    riskTogglerSwitch.style.color = "#f6821f";
    riskTogglerSwitch.style.left = "0rem";
    setTimeout(()=>{
      riskTogglerSwitch.innerText = "ETF";
      riskTogglerSwitch.style.color = "#fff";
    },1000)
    
    toggle = 1;
  }
});

// For Slider Progress Bar
   let e = document.querySelector('#range-input-desktop')
  e.style.setProperty("--value", e.value);
  e.style.setProperty("--min", e.min ? 1000 : e.min);
  e.style.setProperty("--max", e.max ? 20000 : e.max);
  e.addEventListener("input", () => {
    e.style.setProperty("--value", e.value);
    e.value = parseInt(e.value);
  });

// Setting the value bubble of amount
let range = document.querySelector("#range-input-desktop");
let bubble = document.querySelector("#price");

range.addEventListener("input", () => {
  setBubble(range, bubble);
});

function setBubble(range, bubble) {
  const val = range.value;
  const price = range.value;
  const min = range.min ? range.min : 1000;
  const max = range.max ? range.max : 20000;
  const newVal = Number(((val - min) * 100) / (max - min));
  bubble.innerText = `₹${price}`;
  bubble.style.left = `calc(${newVal}% + (${-4.1 - newVal * 0.015}rem))`;
}

})

const handleInvestemt = () =>{
  let gatewayObj = {
      brokers: [
        'ZERODHA',
        'UPSTOX',
        'ANGELBROKING'
      ],
      affiliateBrokers: [],
      preferredBroker: '',
      partnerDetails: {},
      orders: [
        {
          isin: 'INE259A01022',
          scripSymbol: '',
          scripExchange: 'NSE',
          scripPrice: 0,
          scripQty: 1,
          orderType: 'Market',
          orderSide: 'BUY'
        },
        {
          isin: 'INE179A01014',
          scripSymbol: '',
          scripExchange: 'NSE',
          scripPrice: 0,
          scripQty: 1,
          orderType: 'Market',
          orderSide: 'BUY'
        }
      ],
    wealthBasketDisplayName: 'Test',
    wealthBasketName: '',
    sellFirstBuyNext: '',
    qtyEditable: '',
    intent: ''
  }
  // triggerGateway(gatewayObj)
}
  return <section id="side-content" className='side-content-for-desktop'>
  <div class="returns">
    <div class="ret-heading">
      <h1>Returns</h1>
      <div class="custom-select">
        <select>
          <option value="Since Inception">Since Inception</option>
          <option value="Since Inception">Since Inception</option>
          <option value="Option 2">Option 2</option>
          <option value="Option 3">Option 3</option>
        </select>
      </div>
    </div>
    <h1 class="ret-percentage">+{Number(props.data.returns).toFixed(2)}%</h1>
    <a href="#" onClick={handleInvestemt}>Invest Now</a>
  </div>
  <div class="riskometer">
    <h1>Riskometer</h1>
    <div class="risk-toggle">
      <div class="switch">ETF</div>
      <span>ETF</span>
      <span>Benchmark</span>
    </div>
    <img src={RiskometerImg} alt="" />
  </div>
  <div class="cal-returns">
    <h1>Calculate Returns</h1>
    <p>Your investment amount</p>
    <div class="input-range">
      <div class="amount-input">
        <div id="price" class="amount-bubble">₹ 1000</div>
          <input
            id='range-input-desktop'
          type="range"
          max={20000}
            min={1000}
          name="amount"
          value={1000}
          class="styled-slider slider-progress"
        />
      </div>
    </div>
    <p>Investment Time</p>
    <div class="invest-time">
      <select>
        <option value="6 Months">6 Months</option>
        <option value="Option 1">Option 1</option>
        <option value="Option 2">Option 2</option>
      </select>
    </div>
    <div class="cal-bottom">
      <p>Est. Returns <span>% rtn</span></p>
      <p>₹ 8,600 <span>72%</span></p>
    </div>
  </div>
  <div class="documents">
    <h1>Documents</h1>
    <div class="documents-container">
      <p className="docs-title">
        <span>
         Factsheet
        <a href="#"><img src={InfoBtn} alt="" /></a>
        </span>
        <a href="#"><img src={DownloadBtn} alt="" /></a>
      </p>
      <p className="docs-title">
        <span>
          SID
          <a href="#"><img src={InfoBtn} alt="" /></a>
        </span>
        <a href="#"><img src={DownloadBtn} alt="" /></a>
      </p>
      <p className="docs-title">
        <span
          >KIM
          <a href="#"><img src={InfoBtn} alt="" /></a></span><a href="#"><img src={DownloadBtn} alt="" /></a>
      </p>
    </div>
  </div>
</section>

};

export default DetailsPageSideContent;
